import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { getElement } from "../../components/element/element";
import { initializeApp } from "firebase/app";
import { getDatabase, ref, set, child, get, update, remove } from "firebase/database";
import { getStorage, ref as sRef, uploadBytesResumable, getDownloadURL, list } from "firebase/storage";
import './admin.css';
import { getStorage } from "firebase/storage";
import { getDatabase } from "firebase/database";
import { getPhotosList } from "../../components/photosList/photosList";

export function getAdminPage() {
    const adminPage = getElement('div', 'admin');
    const container = getElement('div', 'container', 'admin__container');
    container.classList.add('log');

    const inpLogin = getElement('input', 'admin__login');
    inpLogin.placeholder = 'Логин';
    inpLogin.value = 'admin@mail.ru';
    const inpPassword = getElement('input', 'admin__password');
    inpPassword.value = '123456';
    inpPassword.placeholder = 'Пароль';
    const logBtn = getElement('button', 'admin__btn');
    logBtn.textContent = 'ok';

    const adminForm = getElement('form', 'admin__form');
    const inpNameRu = getElement('input', 'admin__input', 'admin__name', 'admin__input--ru');
    inpNameRu.required = 'required'; 
    inpNameRu.placeholder = 'Название Ru';
    const inpNameEng = getElement('input', 'admin__input', 'admin__name', 'admin__input--eng');
    inpNameEng.required = 'required'; 
    inpNameEng.placeholder = 'Название Eng';
    const inpCategory = getElement('input', 'admin__input', 'admin__category');
    inpCategory.required = 'required'; 
    inpCategory.placeholder = 'Категория';
    const inpCover = getElement('button', 'admin__cover');
    inpCover.textContent = 'Добавить обложку';
    const inpImages = getElement('button', 'admin__images');
    inpImages.textContent = 'Добавить фото';
    inpImages.type ='button';
    const submitBtn = getElement('button', 'admin__submit');
    submitBtn.textContent = 'Добавить';
    submitBtn.type = 'submit';

    const imagesDiv = getElement('div', 'admin__preview');

    const firebaseConfig = {
        apiKey: "AIzaSyBlFYLAI5C8bvQJHGTm85obSILYb-V620s",
        authDomain: "photographer-site-aae97.firebaseapp.com",
        databaseURL: "https://photographer-site-aae97-default-rtdb.firebaseio.com",
        projectId: "photographer-site-aae97",
        storageBucket: "photographer-site-aae97.appspot.com",
        messagingSenderId: "506799504003",
        appId: "1:506799504003:web:205497e14804b4513479b6"
    };

    const app = initializeApp(firebaseConfig);
    const auth = getAuth(app);
    const realdb = getDatabase();

    let updatedId = '';

    let portfolio = [];
    function getPortfolioFromDB() {
        const dbref = ref(realdb);
        const portfolioArray = [];

        get(child(dbref, 'portfolio/'))
        .then((snapshot) => {
            console.log(snapshot);
            snapshot.forEach(item => {
                portfolioArray.push(item.val());
            });
            portfolio = portfolioArray;
        }).then(() => {
            createPortfolioItems(portfolio);
        })
    }

    function createPortfolioItems(portfolio) {
        container.innerHTML = '';
        const itemsList = getElement('ul', 'items-list', 'list-reset');

        portfolio.forEach(item => {
            const li = getElement('li', 'items-list__item');
            const nameDiv = getElement('div');
            const categoryDiv = getElement('div');
            const actionDiv = getElement('div');
            nameDiv.textContent = item.nameRu;
            categoryDiv.textContent = item.category;
            const removeBtn = getElement('button');
            removeBtn.textContent = 'Удалить';

            removeBtn.addEventListener('click', () => {
                if (confirm(`Remove this item?(${item.nameRu})`)) {
                    const itemToRemove = portfolio.find(element => element.id === item.id);
                    if (itemToRemove) {
                        const itemRef = ref(realdb, `portfolio/${itemToRemove.id}`);
                        remove(itemRef)
                            .then(() => {
                                console.log('Item removed successfully');
                                getPortfolioFromDB();
                            })
                            .catch((error) => {
                                console.error('Error removing item:', error);
                            });
                    } else {
                        console.log('Item not found in portfolio');
                    }
                }
            });

            const editBtn = getElement('button');
            editBtn.textContent = 'Редактировать';
            const editedName = getElement('div', 'edited-name');
            const editedCancel = getElement('button', 'edited-cancel');
            editedCancel.textContent = 'Отмена';
            editedCancel.type = 'button';

            editedCancel.addEventListener('click', () => {
                updatedId = '';
                document.querySelector('.edited-name').remove();
                document.querySelector('.edited-cancel').remove();
                submitBtn.textContent = 'Добавить';
                inpNameRu.value = '';
                inpNameEng.value = '';
                inpCategory.value = '';
                Files = [];
                createImgPreviews();
            });

            editBtn.addEventListener('click', () => {
                inpNameEng.value = item.nameEng;
                inpNameRu.value = item.nameRu;
                inpCategory.value = item.category;
                updatedId = item.id;
                console.log(item.linksOfImages);
                
                editedName.textContent = `Редактировние ${item.nameRu}`;

                adminPanel.prepend(editedName, editedCancel)
                submitBtn.textContent = 'Изменить';
            });

            actionDiv.append(editBtn, removeBtn);
            li.append(nameDiv, categoryDiv, actionDiv);
            itemsList.append(li);
        });

        const adminPanel = getAdminPanel();
        container.append(itemsList, adminPanel);
    }

    const randomId = () => {
        return Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15);
    };
    
    function openFileDialog() {
        let input = document.createElement('input');
        input.type = 'file';
        input.multiple = 'multiple';
    
        input.onchange = (e) => {
            assignImgsToFilesArray(e.target.files);
            createImgPreviews();
        }
    
        input.click();
    }

    let Files = [];
    let FileReaders = [];

    function assignImgsToFilesArray(thisFiles) {
        let num = Files.length + thisFiles.length;
        let loopLim = (num <= 20) ? thisFiles.length : (20 - Files.length);
        
        for (let i = 0; i < loopLim; i++) {
            Files.push(thisFiles[i]);
        }
    
        if (num > 20) alert('!!!');

        inpImages.textContent = `Загружено ${Files.length}/20`;
        console.log(Files);
    }

    function uploadAllImages(id) {
        linksOfImages = [];
    
        for (let i = 0; i < Files.length; i++) {
            uploadImageToStorage(Files[i], id);
        }
    }
    
    function uploadImageToStorage(imgToUpload, currentId) {
        const imgNo = Files.indexOf(imgToUpload); 

    
        const metadata = {
            contentType: imgToUpload.type
        };
    
        const storage = getStorage();
    
        const imgAddress = "TheImages/" + currentId + "/img#" + (imgNo + 1);
    
        const storageRef = sRef(storage, imgAddress);
    
        const uploadTask = uploadBytesResumable(storageRef, imgToUpload, metadata);
    
        uploadTask.on('state_changed', (snapshot) => {
        
        },
    
        (error) => {
            alert('error');
        },
    
        () => {
            getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
                linksOfImages[imgNo] = downloadURL;
                addItem(currentId);
            });
        });
    }

    function addItem(id) {
        set(ref(realdb, "portfolio/" + id), {
            nameRu: inpNameRu.value,
            nameEng: inpNameEng.value,
            linksOfImages: linksOfImages,
            linkOfCover: linksOfImages[0],
            category: inpCategory.value.trim(),
            id: id
        }).then(() => {
            alert('Портфолио обновлено!');
            getPortfolioFromDB();
            updatedId = '';
            submitBtn.textContent = 'Добавить';
            if (!updatedId === '') {
                document.querySelector('.edited-name').remove();
            }
            
            createImgPreviews();
        })
    }

    function getAdminPanel() {
        container.classList.remove('log');
        container.append(adminForm);
        adminForm.append(inpNameRu, inpNameEng, inpImages, imagesDiv, inpCategory, submitBtn);
        return adminForm;
    };

    logBtn.addEventListener('click', () => {
        signInWithEmailAndPassword(auth, inpLogin.value, inpPassword.value) 
        .then(() => { alert('Success!');
        container.innerHTML = '';
        getPortfolioFromDB();
        container.classList.remove('log');
        })
        .catch((error) => { 
            alert('Error: Incorrect login or password. Please try again.'); 
        }); 
    });

    inpImages.addEventListener('click', () => {
        openFileDialog();
    })

    adminForm.addEventListener('submit', (event) => {
        event.preventDefault();
        if (inpNameEng.value !== '' && inpNameRu.value !== '' && Files.length > 0) {
            const currentId = randomId();
            if (updatedId === '') {
                uploadAllImages(currentId);
            } else {
                uploadAllImages(updatedId);
            }
        } else {
            alert('error');
        }
    });

    function createImgPreviews() {
        imagesDiv.innerHTML = '';
        for (let i = 0; i < Files.length; i++) {
            (function(index) {
                FileReaders[index] = new FileReader();
    
                FileReaders[index].onload = function() {
                    const div = getElement('div');
                    const button = getElement('button');
                    button.textContent = 'Удалить';
                    let img = document.createElement('img');
    
                    button.addEventListener('click', () => {
                        for (let j = 0; j < Files.length; j++) {
                            if (Files[j].name === button.dataset.value) {
                                Files.splice(j, 1);
                                createImgPreviews(); // обновляем превью после удаления
                            }
                        }
                    });
    
                    img.id = `img-${index}`;
                    img.classList.add('choose__img');
                    img.src = FileReaders[index].result;
                    img.dataset.value = i;
                    button.type = 'button';
                    button.dataset.value = Files[i].name;
                    div.append(img, button);
                    imagesDiv.append(div);
                }
    
                FileReaders[index].readAsDataURL(Files[index]);
            })(i);
        }
        inpImages.textContent = `Загружено ${Files.length}/20`;
    }
    
    container.append(inpLogin, inpPassword, logBtn);
    adminPage.append(container);
    return adminPage;
}